<template>
    <div style="text-align:center;position:relative;">
        <svg :width="svgWidth" :height="svgHeight" style="margin-left:100px;margin-bottom:-40px;" id="svg1"></svg>
        <el-slider id="jjj"
                   v-model="svgProgress"
                   show-input
                   style="width: 500px;float: none"
                           >
        </el-slider>

        <div id="indicator">
        </div>
        <div id="mode">
            <span class="active" style="border-top-right-radius:0;border-bottom-right-radius:0;">Circles</span>
            <span style="border-top-left-radius:0;border-bottom-left-radius:0;position:relative;left:-5px;">Texts</span>
        </div>
        <div id="search1">
            <input type="text" class="form-control">
        </div>
        <div id="button" style="color: white">放大</div>
        <div id="button1" style="color: white">缩小</div>

        <div id="info" :data="entityInfo">

            <p ><a style="color: orange;font-size: 20px;" @click="relatedDocVisible=true">{{entityInfo.name}}</a></p>
            <h3 ><p ><span>实体ID</span>{{entityInfo.identity}}</p></h3>
            <p><span>实体名称</span>{{entityInfo.name}}</p>
            <p><span>实体标签</span>{{entityInfo.label}}</p>
            <p><span>中文名称</span>{{entityInfo.chineseName}}</p>
            <p><span>英文名称</span>{{entityInfo.englishName}}</p>
            <p><span>英文缩写</span>{{entityInfo.abbreName}}</p>


            <el-button type="text" size="small" @click="entityAddFormVisible = true" >添加实体</el-button>

            <el-button type="text" size="small" @click="entityEditBoxAlert()">修改实体</el-button><br>
            <el-button type="text" size="small" @click="tripletAddFormVisible = true" >添加三元组</el-button>

            <el-button type="text" size="small" @click="tripletRelated(entityInfo.identity)">修改三元组</el-button>

        </div>
        <!-- 实体相关三元组列表弹出框-->
        <el-dialog title="关联文档列表" :visible.sync="relatedDocVisible">
            <p ><a style="color: red;font-size: 20px;" :href="'https://baike.baidu.com/item/'+entityInfo.name">{{entityInfo.name}}:关联文档1</a></p>
            <p ><a style="color: orange;font-size: 20px;" :href="'https://baike.baidu.com/item/'+entityInfo.name">{{entityInfo.name}}:关联文档2</a></p>
            <p ><a style="color: blue;font-size: 20px;" :href="'https://baike.baidu.com/item/'+entityInfo.name">{{entityInfo.name}}:关联文档3</a></p>

        </el-dialog>





        <el-dialog title="实体相关三元组列表" :visible.sync="tripletTableVisible" >
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" style="float: left">全选</el-checkbox>
            <el-button type="primary" size="small" @click="tripletUpdate(tripletForm)" style="float: right">批量删除</el-button>

            <el-table
                    :data="tripletList"
                    style="width: 100%"
            >
                <el-table-column
                        width="50">

                    <el-checkbox :checked="checkAll"></el-checkbox>

                </el-table-column>
                <el-table-column
                        prop="id"
                        label="id"
                        width="80">
                </el-table-column>

                <el-table-column
                        prop="source"
                        label="头实体"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="value"
                        label="关系"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="target"
                        label="尾实体"
                        width="100"
                >
                </el-table-column>

                <el-table-column
                        fixed="right"
                        label="操作"
                        width="100">
                    <template slot-scope="scope">
                        <el-button @click="tripletUpdateBox(scope.row.id)" type="text" size="small">修改</el-button>
                        <el-button @click="tripletDelete(scope.row.id)" type="text" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 实体相关三元组列表弹出框-->
        </el-dialog>

        <el-dialog title="修改三元组" :visible.sync="tripletFormVisible">

            <el-form :model="tripletForm">

                <span class="demonstration">头实体</span>
                <el-cascader
                        v-model="tripletForm.source"
                        :options="sourceOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader><br/>
                <span class="demonstration">关系</span>
                <el-cascader
                        v-model="tripletForm.value"
                        :options="relationOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader><br/>
                <span class="demonstration">尾实体</span>
                <el-cascader
                        v-model="tripletForm.target"
                        :options="targetOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="tripletUpdate(tripletForm)">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="添加三元组" :visible.sync="tripletAddFormVisible">

            <el-form :model="tripletForm">

                <span class="demonstration">头实体</span>
                <el-cascader
                        v-model="tripletForm.source"
                        :options="sourceOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader><br/>
                <span class="demonstration">关系</span>
                <el-cascader
                        v-model="tripletForm.value"
                        :options="relationOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader><br/>
                <span class="demonstration">尾实体</span>
                <el-cascader
                        v-model="tripletForm.target"
                        :options="targetOptions"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange(entityName)"></el-cascader>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="tripletAddFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="tripletAdd(tripletForm)">确 定</el-button>
            </div>
        </el-dialog>



        <el-dialog title="修改实体信息" :visible.sync="entityEditFormVisible">

            <el-form :model="entityForm">

                <el-form-item label="实体名" :label-width="formLabelWidth">
                    <el-input v-model="entityForm.name" autocomplete="off" ></el-input>
                </el-form-item>
                <span class="demonstration">实体标签</span>
                <el-cascader
                        v-model="entityForm.label"
                        :options="entityLabels"
                        :props="{ expandTrigger: 'hover' }"

                        @change="handleChange(entityName)"></el-cascader>
                <el-form-item label="中文名称" :label-width="formLabelWidth">
                    <el-input v-model="entityForm.chineseName" autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="英文名称" :label-width="formLabelWidth">
                    <el-input v-model="entityForm.englishName" autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="英文缩写" :label-width="formLabelWidth">
                    <el-input v-model="entityForm.abbreName" autocomplete="off" ></el-input>
                </el-form-item>


            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="entityEditFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="entityUpdate(entityForm)">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="添加实体" :visible.sync="entityAddFormVisible">

            <el-form :model="entityAddForm">
                <el-form-item label="实体名" :label-width="formLabelWidth">
                    <el-input v-model="entityAddForm.name" autocomplete="off" ></el-input>
                </el-form-item>
                <span class="demonstration">实体标签</span>
                <el-cascader
                        v-model="entityAddForm.label"
                        :options="entityLabels"
                        :props="{ expandTrigger: 'hover' }"

                        @change="handleChange(entityName)"></el-cascader>
                <el-form-item label="中文名称" :label-width="formLabelWidth">
                    <el-input v-model="entityAddForm.chineseName" autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="英文名称" :label-width="formLabelWidth">
                    <el-input v-model="entityAddForm.englishName" autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="英文缩写" :label-width="formLabelWidth">
                    <el-input v-model="entityAddForm.abbreName" autocomplete="off" ></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="entityAddFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="entityAdd(entityAddForm)">确 定</el-button>
            </div>
        </el-dialog>




    </div>

</template>

<script>
    import * as d3 from 'd3'
    import $ from 'jquery'
    const axios = require('axios');

    // const axios = require('axios')


    export default {

        data() {
            return {
                entityName:"",
                entityInfo: {
                    identity: "实体id",
                    name: "实体名称",
                    label: '实体标签',
                    chineseName: "中文名称",
                    englishName: '英文名称',
                    abbreName: '英文缩写'
                },

                info: {
                    "": {}
                },
                graph: {
                    "nodes": {},
                    "links": {}
                },
                svgHeight:660,
                svgWidth:960,
                svgProgress:50,
                checkAll:true,
                isIndeterminate:false,
                relatedDocVisible:false,
                tripletTableVisible: false,  //实体相关三元组展示弹出框
                tripletFormVisible: false,  //三元组修改弹出框
                tripletAddFormVisible:false,
                entityAddFormVisible: false,
                entityEditFormVisible:false,

                formLabelWidth: '120px',

                tripletList:
                    [
                        {
                            id: 1,
                            source: '头实体',
                            value: '关系',
                            target: '尾实体'

                        }],
                form: {
                    id: '123',
                    source: '',
                    value: '',
                    target: '',

                },
                tripletForm:{
                    id:"",
                    source:[""],
                    target:[""],
                    value:"",

                },
                sourceOptions: [{
                    children: [
                        {
                            label: "多媒体呼叫控制",
                            value: "多媒体呼叫控制"
                        }
                    ],
                    label: "控制",
                    value: "控制"
                },],
                targetOptions: [{
                    children: [
                        {
                            label: "多媒体呼叫控制",
                            value: "多媒体呼叫控制"
                        }
                    ],
                    label: "控制",
                    value: "控制"
                },],


                relationOptions:[{
                    value:"",
                    label:""
                }],

                entityForm: {
                    identity: "",
                    name: "",
                    label: ["",],
                    chineseName: "中文名称",
                    englishName: '英文名称',
                    abbreName: '英文缩写'
                },
                entityAddForm: {
                    identity: "",
                    name: "",
                    label: ["",],
                    chineseName: "",
                    englishName: '',
                    abbreName: ''
                },


                entityLabels:[{
                    value:"",
                    label:""
                },],


                // "label",
                entityOptions: [{
                    children: [
                        {
                            label: "多媒体呼叫控制",
                            value: "多媒体呼叫控制"
                        }
                    ],
                    label: "控制",
                    value: "控制"
                },]



            }
        },
        mounted() {
            const _this = this;


            let info;

            $.ajax({
                url: '/e',
                type: 'get',
                async: false,
                dataType: 'json',
                success: function(response) {
                    info = response
                }
            });




            const svg = d3.select("#svg1"),
                width = +svg.attr("width"),
                height = +svg.attr("height");

            // var names = ['\u534f\u8bae', '\u6570\u636e', '数据', '数据单元', '服务', '硬件'];
            const names = ['设备',  '信道',  '协议',  '参数', '系统','数据',  '模式', '标识', '模块', '功能', '电路', '网络', '消息', '接口', '服务', '控制', '终端', '网关'];


            const colors = [ '#FFA07A', '#FF7F50',  '#FF6347', '#FFFF00', '#FFFFE0', '#FFD700', '#FFF8DC', '#F5DEB3', '#00FF7F', '#90EE90', '#32CD32', '#00FF00', '#7FFF00', '#ADFF2F', '#FAFAD2', '#AFEEEE', '#00FFFF', '#7FFFD4', '#00BFFF', '#F0F8FF', '#00CED1', '#FAFAD2'];

            for (var i = 0; i < names.length; i++) {
                $('#indicator').append("<div><span style='background-color:" + colors[i] + "'></span>" + names[i] + "</div>");
            }



            const simulation = d3.forceSimulation()
                .force("link", d3.forceLink().id(function (d) {
                    return d.id;
                }))
                .force("charge", d3.forceManyBody())

                .force("center", d3.forceCenter(width / 2, height / 2));

            let graph;


            $.ajax({
                url: '/links',
                type: 'get',
                async: false,
                dataType: 'json',
                success: function(response) {
                    graph = response
                }
            });






            let link = svg.append("g")
                .attr("class", "links")
                .selectAll("line")
                .data(graph.links)

                .enter().append("line")
                // eslint-disable-next-line no-unused-vars
                .attr("stroke-width", function (d) {
                    // return Math.sqrt(d.value);
                    return 2;
                });

            const node = svg.append("g")
                .attr("class", "nodes")
                .selectAll("circle")
                .data(graph.nodes)
                .enter().append("circle")
                .attr("r", function (d) {
                    return d.size //圆圈大小
                })
                .attr("fill", function (d) {
                    // return color(d.group);
                    return colors[d.group];  //实体类型
                    // return names.indexOf(d.id)
                })
                .attr('stroke', 'none')
                .attr('name', function (d) {
                    return d.id;
                })
                .text(function (d) {
                    return d.id

                })
                .call(d3.drag()
                    .on("start", dragstarted)
                    .on("drag", dragged)
                    )
                    .on("click",click)

                .classed("fixed",d => d.fx !== undefined);


            const text = svg.append("g")
                .attr("class", "texts")
                .selectAll("text")
                .data(graph.nodes)
                .enter().append("text")
                .attr("font-size", function (d) {
                    return d.size
                })
                .attr("fill", function (d) {
                    // return color(d.group);
                    return colors[d.group];
                })
                .attr('name', function (d) {
                    return d.id;
                })
                .text(function (d) {
                    if (d.id.length>3) return d.id.slice(0,3)+ '...'
                    return d.id;
                })
                .attr('text-anchor', 'middle')
                .call(d3.drag()
                    .on("start", dragstarted)
                    .on("drag", dragged)
                   );


            node.append("title")
                .text(function (d) {
                    return d.id;
                });

            text.append("title")
                .text(function (d) {
                    return d.id;
                });

            simulation
                .nodes(graph.nodes)
                .on("tick", ticked);

            simulation.force("link")
                .links(graph.links);

            function transx(x,low,high) {
                x  = x * 3
                return x < low ? -(x % high) : x > high ? x%high : x;


            }


            function transy(x,low,high) {
                x  = x * 2
                return x < low ? -(x % high) : x > high ? x%high : x;


            }



            function ticked() {
                link
                    .attr("x1", function (d) {
                        return transx(d.source.x,0,width);
                    })
                    .attr("y1", function (d) {
                        return transy(d.source.y,0,height);
                    })
                    .attr("x2", function (d) {
                        return transx(d.target.x,0,width);
                    })
                    .attr("y2", function (d) {
                        return transy(d.target.y,0,height);
                    });

                node
                    .attr("cx", function (d) {
                        return transx(d.x,0,width)
                    })
                    .attr("cy", function (d) {
                        return transy(d.y,0,height)
                    });

                text.attr('transform', function (d) {
                    return 'translate(' + transx(d.x,0,width) + ',' + (transy(d.y,0,height) + d.size / 2) + ')';
                });
            }



            let dragging = false;


            function click(event, d) {
                delete d.x;
                delete d.y;
                d3.select(this).classed("fixed", false);
                simulation.alpha(1).restart();
        }



            function dragstarted() {
                d3.select(this).classed("fixed", true);
            }
            //
            // function borderCheck(x, lo, hi) {
            //     // return x < lo ? -x%width : x > hi ? x%hi : x;
            //
            //     return x + lo - lo + hi - hi
            // }

            function clamp(x, lo, hi) {
                return x < lo ? lo + 40 : x > hi ? hi - 40 : x;
            }

            function dragged( d) {
                d.fx = clamp(d3.event.x, 0, width);
                d.fy = clamp(d3.event.y, 0, height);
                simulation.alpha(1).restart();
            }



            // eslint-disable-next-line no-unused-vars
            $('#mode span').click(function (event) {
                $('#mode span').removeClass('active');
                $(this).addClass('active');
                if ($(this).text() == 'Circles') {
                    $('.texts text').hide();
                    $('.nodes circle').show();
                } else {
                    $('.texts text').show();
                    $('.nodes circle').hide();
                }
            });

            let isClick = false;

            $('#svg1').on('click', '.nodes circle', function () {
                isClick = !isClick
                d3.select(this).classed("fixed", isClick);
                simulation.alpha(1).restart();
                if (isClick){
                    if (!dragging) {
                        var name = $(this).attr('name');
                        _this.getEntityInto(info[name]['identity'])


                        d3.select('#svg1 .nodes').selectAll('circle').attr('class', function (d) {
                            if (d.id == name) {
                                return '';
                            }

                            for (var i = 0; i < graph.links.length; i++) {
                                if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                    return '';
                                }
                                if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                    return '';
                                }
                            }
                            return 'inactive';
                        });
                        d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
                            if (d.source.id == name || d.target.id == name) {
                                return '';
                            } else {
                                return 'inactive';
                            }
                        });
                    }
                }
                else{
                    if (!dragging) {
                        d3.select('#svg1 .nodes').selectAll('Circle').attr('class', '');
                        d3.select('#svg1 .links').selectAll('line').attr('class', '');
                    }
                }


            });



            // eslint-disable-next-line no-unused-vars
            $('#svg1').on('click', '.texts text', function (event) {
                isClick = !isClick
                if (isClick) {
                    if (!dragging) {
                        var name = $(this).attr('name');
                        _this.getEntityInto(info[name]['identity'])


                        d3.select('#svg1 .texts').selectAll('text').attr('class', function (d) {
                            if (d.id == name) {
                                return '';
                            }

                            for (var i = 0; i < graph.links.length; i++) {
                                if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                    return '';
                                }
                                if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                    return '';
                                }
                            }
                            return 'inactive';
                        });
                        d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
                            if (d.source.id == name || d.target.id == name) {
                                return '';
                            } else {
                                return 'inactive';
                            }
                        });
                    }
                }
                else {
                    if (!dragging) {
                        d3.select('#svg1 .texts').selectAll('text').attr('class', '');
                        d3.select('#svg1 .links').selectAll('line').attr('class', '');
                    }
                }
            });


            $('#indicator div').click(function () {
                if (!dragging) {
                    var name = $(this)[0].innerText
                    _this.getEntityInto(info[name]['identity'])


                    d3.select('#svg1 .nodes').selectAll('circle').attr('class', function (d) {
                        if (d.id == name) {
                            return '';
                        }

                        for (var i = 0; i < graph.links.length; i++) {
                            if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                return '';
                            }
                            if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                return '';
                            }
                        }
                        return 'inactive';
                    });

                    d3.select('#svg1 .texts').selectAll('text').attr('class', function (d) {
                        if (d.id == name) {
                            return '';
                        }

                        for (var i = 0; i < graph.links.length; i++) {
                            if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                return '';
                            }
                            if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                return '';
                            }
                        }
                        return 'inactive';
                    });
                    d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
                        if (d.source.id == name || d.target.id == name) {
                            return '';
                        } else {
                            return 'inactive';
                        }
                    });
                }
            });




            // eslint-disable-next-line no-unused-vars
            $('#search1 input').keyup(function (event) {
                if ($(this).val() == '') {
                    d3.select('#svg1 .texts').selectAll('text').attr('class', '');
                    d3.select('#svg1 .nodes').selectAll('circle').attr('class', '');
                    d3.select('#svg1 .links').selectAll('line').attr('class', '');
                } else {
                    var name = $(this).val();

                    d3.select('#svg1 .nodes').selectAll('circle').attr('class', function (d) {
                        if (d.id == name) {
                            return '';
                        }

                        for (var i = 0; i < graph.links.length; i++) {
                            if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                return '';
                            }
                            if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                return '';
                            }
                        }
                        return 'inactive';
                    });
                    d3.select('#svg1 .texts').selectAll('text').attr('class', function (d) {
                        if (d.id == name) {
                            return '';
                        }

                        for (var i = 0; i < graph.links.length; i++) {
                            if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                                return '';
                            }
                            if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                                return '';
                            }
                        }
                        return 'inactive';
                    });
                    d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
                        if (d.source.id == name || d.target.id == name) {
                            return '';
                        } else {
                            return 'inactive';
                        }
                    });


                }
            });


            $('#button').click(function () {
                d3.select("#svg1 .texts").selectAll('text').attr('font-size', function (d) {
                    d.size =  d.size + 2
                    return d.size

                });
            });

            $('#button1').click(function () {

                d3.select("#svg1 .texts").selectAll('text').attr('font-size', function (d) {
                    d.x = d.x - 20
                    d.y = d.y - 20
                    d.size =  d.size - 2
                    return d.size
                });

            });

            $('#svg2').on('mouseenter', 'g.row', function (event) {
                event.preventDefault();
                $('#svg2 text.title').attr('fill-opacity', 0);
                $('#svg2 text.title[name="' + $(this).attr('id') + '"]').attr('fill-opacity', 1);
            });

            $('#svg2').on('mouseleave', 'g.row', function (event) {
                event.preventDefault();
                $('#svg2 text.title').attr('fill-opacity', 0);
            });


        },
        methods:{

            getEntityInto(entity_id){
                const _this = this
                axios.get('/entity/'+entity_id).then(function(resp){
                    _this.entityInfo = resp.data['entity']
                    _this.entityForm = resp.data['entity']

                })
            },
            tripletRelated(entity_id){
                const _this = this;
                _this.tripletTableVisible = true;

                axios.get('/entity/'+entity_id).then(function(resp){


                    _this.tripletList = resp.data['triplets']

                })

            },


            tripletAddBox(){
                const _this = this;
                _this.tripletAddFormVisible = true;

            },
            tripletAdd(form){
                const _this = this;

                axios({
                    method:"POST",
                    url:"/addTriplet",
                    data:{
                        form
                    }
                }).then(response=>{
                    if(response.status==200){

                        alert("插入成功")
                        location.reload()

                    }
                    else alert("插入失败")
                })

                _this.tripletAddFormVisible = false;
                _this.tripletFormVisible = false;
                _this.tripletTableVisible = false



            },
            tripletUpdateBox(triplet_id){
                const _this = this;
                _this.tripletFormVisible = true;
                axios.get('/selTripletById/'+triplet_id).then(function(resp){
                    _this.tripletForm= resp.data
                })
            },
            //提交修改信息

            tripletUpdate(form){
                const _this = this;
                axios({
                    method:"POST",
                    url:"/updateTriplet",
                    data:{
                        form
                    }
                }).then(response=>{
                    if(response.status==200)alert("修改成功")
                    else alert("修改失败")
                })

                _this.tripletFormVisible = false;
                _this.tripletTableVisible = false

            },
            tripletDelete(triplet_id){
                const _this = this;

                axios.get('/delTripletById/'+triplet_id).then(function(resp){
                    _this.form = resp.data


                })
                alert("删除成功")
                location.reload()

            },
            entityAdd(form){
                const _this = this
                form.label = form.label[0]
                axios({
                    method:"POST",
                    url:"/addEntity",
                    data:{
                        form
                    }
                }).then(response=>{
                    if (response.status==200) {
                        alert("添加成功！");
                        _this.entityAddFormVisible = false;
                        _this.entityEditFormVisible = false;
                        location.reload()
                    }
                    else alert(response.status);
                })
                _this.entityAddFormVisible = false;
            }
            ,
            handleChange(value) {
                console.log(value[1]);
            },
            handleCheckAllChange(){
                const _this = this;
                if (_this.checkAll == true) _this.checkAll = false
                else _this.checkAll = true
            },

            entityUpdate(form){

                axios({
                    method:"POST",
                    url:"/editEntity",
                    data:{

                        form
                    }
                }).then(response=>{
                    if(response.status ==200) alert("修改成功")

                    else alert("修改失败")
                })
                this.entityEditFormVisible = false;

            },
            entityEditBoxAlert(){
                const _this = this;
                _this.entityEditFormVisible = true;
                axios.get('/selectEntityLabels').then(function(resp){
                    _this.entityLabels = resp.data
                })
            }




        },
        created() {
            const _this = this;
            axios.get('/getEntitiesByLabel').then(function (resp) {
                _this.sourceOptions = resp.data
                _this.targetOptions = resp.data

            })

            axios.get('/getRelations').then(function (resp) {
                _this.relationOptions = resp.data
            })

            axios.get('/selectEntityLabels').then(function(resp){
                _this.entityLabels = resp.data
            })

        },
        watch:{
            svgProgress(){

                this.svgHeight = 660 + this.svgProgress - 50;
                this.svgWidth = 960 + this.svgProgress - 50;


            }

        }


    }


</script>

