import Vue from 'vue'
import VueRouter from 'vue-router'
import TestPage from "@/components/TestPage";



Vue.use(VueRouter)




const routes = [
    {
      path: '/',
        component:TestPage
    },
    {
        path:'/test',
        component:TestPage
    },
  

]
const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router